*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1 {
  font-family: var(--font);
  color: var(--main-text-color);
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.2;
}

h2 {
  font-family: var(--font);
  color: var(--main-text-color);
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.16;
}

h3 {
  font-family: var(--font);
  color: var(--main-text-color);
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.16;
}

p {
  font-family: var(--font);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 480px;
    margin: 0 auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 16px;
  }
}

:root {
  --main-bg-color: #fefefe;
  --main-text-color: #2c2c2c;
  --menu-text-color: #000;
  --hover-green-color: #bbf330;
  --hover-btn1-color: #b5ff00;
  --main-yellow-color: #ffe550;
  --hover-arrow-color: #7eb101;
  --hover-btn-color: #597d00;
  --font: "Roboto", sans-serif;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.header__container {
  background-color: var(--main-bg-color);
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .nav {
    z-index: 4;
    width: 130px;
    opacity: 0;
    pointer-events: none;
    transition: opacity var(--animation);
    background: #fefefe;
    border-radius: 30px 0 30px 30px;
    position: absolute;
    top: 50px;
    right: 20px;
    overflow: hidden;
    box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
  }

  .nav__link__header {
    font-family: var(--font);
    color: var(--menu-text-color);
    width: 100%;
    height: 100%;
    color: var(--main-text-color);
    transition: background-color var(--animation);
    padding: 16px 10px 16px 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.1875;
    text-decoration: none;
    display: block;
  }

  .nav__link__header:hover, .nav__link__header:focus {
    background-color: var(--hover-green-color);
    color: var(--main-text-color);
    text-decoration: none;
  }

  .btn__header {
    display: none;
  }

  .btn-mobile-menu {
    color: var(--hover-arrow-color);
    cursor: pointer;
    transition: color var(--animation);
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
  }

  .btn-mobile-menu__close {
    position: absolute;
    top: 4px;
    right: 4px;
  }

  .btn-mobile-menu:hover, .btn-mobile-menu:focus {
    color: var(--main-yellow-color);
  }

  .btn-mobile-menu__icon {
    fill: currentColor;
  }

  .is-open {
    opacity: 1;
    pointer-events: initial;
  }
}

@media screen and (min-width: 768px) {
  .nav, .nav__list {
    align-items: center;
    gap: 28px;
    display: flex;
  }

  .nav__link__header {
    font-family: var(--font);
    color: var(--main-text-color);
    transition: color var(--animation);
    color: var(--main-text-color);
    font-size: 14px;
    font-style: normal;
    line-height: 1.14286;
    text-decoration: none;
    display: block;
  }

  .nav__link__header:hover, .nav__link__header:focus {
    color: var(--hover-green-color);
    text-decoration: none;
  }

  .btn__header {
    width: 98px;
    height: 32px;
    font-family: var(--font);
    color: var(--main-text-color);
    background-color: var(--hover-green-color);
    cursor: pointer;
    transition: background-color var(--animation);
    border: none;
    border-radius: 30px;
    padding: 0;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.5;
    display: block;
  }

  .btn__header:hover, .btn__header:focus {
    background-color: var(--hover-btn1-color);
  }

  .btn-mobile-menu {
    display: none;
  }
}

