.list {
    list-style: none;
    padding: 0;
    margin: 0;
}
.header{
   box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1); 
}
.header__container {  
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: var(--main-bg-color);
    position: relative;    
}

@media screen and (max-width: 767px) {
    .nav {
        position: absolute;
        top: 50px;
        right: 20px;
        z-index: 4;
        width: 130px;
        overflow: hidden;
        opacity: 0;
        pointer-events: none;
        background: #FEFEFE;
        box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
        border-radius: 30px 0px 30px 30px;

        transition: opacity var(--animation);
    }

    .nav__link__header {
        font-family: var(--font);
        font-weight: 400;
        color: var(--menu-text-color);
        padding: 16px 10px 16px 16px;
        width: 100%;
        height: 100%;
        font-size: 16px;
        line-height: calc(19 / 16);
        text-decoration: none;
        color: var(--main-text-color);
        display: block;
        transition: background-color var(--animation);
        &:hover,
        &:focus {
            background-color: var(--hover-green-color);
            color: var(--main-text-color);
            text-decoration: none;
        }
    }

    .btn__header {
        display: none;
    }

    .btn-mobile-menu {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;

        border: 0;
        background-color: transparent;
        color: var(--hover-arrow-color);
        cursor: pointer;

        transition: color var(--animation);
    }

    .btn-mobile-menu__close {
        position: absolute;
        top: 4px;
        right: 4px;
    }

    .btn-mobile-menu:hover,
    .btn-mobile-menu:focus {
        color: var(--main-yellow-color);
    }

    .btn-mobile-menu__icon {
        fill: currentColor;
    }

    .is-open {
        opacity: 1;
        pointer-events: initial;
    }
}

@media screen and (min-width: 768px) {
    .nav {
        display: flex;
        gap: 28px;
        align-items: center;
    }

    .nav__list {
        display: flex;
        align-items: center;
        gap: 28px;
    }

    .nav__link__header {
        font-family: var(--font);
        font-style: normal;
        font-size: 14px;
        line-height: calc(16 / 14);
        color: var(--main-text-color);
        transition: color var(--animation);
        text-decoration: none;
        color: var(--main-text-color);
        display: block;
        &:hover,
        &:focus {
            color: var(--hover-green-color);
            text-decoration: underline;
            text-decoration: none
        }
    }

    .btn__header {
        display: block;
        width: 98px;
        height: 32px;
        padding: 0;
        font-family: var(--font);
        font-weight: 500;
        font-size: 13px;
        line-height: 1.5;
        color: var(--main-text-color);
        background-color: var(--hover-green-color);
        border-radius: 30px;
        border: none; 
        
        cursor: pointer;
        
        transition: background-color var(--animation);

        &:hover,
        &:focus {
            background-color: var(--hover-btn1-color);
    }
    }

    .btn-mobile-menu {
        display: none;
    }
}











